@import '../../styles/base-theme.scss';
@import '../../styles/mixins.scss';

.shopCart {
  padding-top: 24px;
  margin-bottom: 56px;

  padding-left: 16px;
  padding-right: 16px;

  @media (min-width: 576px) {
    padding-top: 40px;
    margin-bottom: 64px;
  }

  @media (min-width: 1199px) {
    padding-right: 0;
    margin-bottom: 80px;
  }

  &__title {
    @include h1;
    margin-bottom: 32px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 32px;

    @media (min-width: 768px) {
      flex-direction: row;
      gap: 16px;
    }
  }
}
